import React from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Route,
} from "react-router-dom";
import Navbar from './components/Navbar';
import Home from './contents/Home';
import About from './contents/About';
import Education from './contents/Education';
import Teaching from './contents/Teaching';
import Skills from './contents/Skills';
import Contact from './contents/Contact';
import Research from './contents/Research';
import Publication from './contents/Publication';
import Honors from './contents/Honors';


function App() {
  return (
    <Router  basename={process.env.PUBLIC_URL}>
    <div className="App">
    <Navbar />
    <Route exact path="/">
    <Home/>
    </Route>
    <Route path="/about">
    <About/>
    </Route>
    <Route path="/education">
    <Education />
    </Route>
    <Route path="/teaching">
    <Teaching />
    </Route>
    <Route path="/skills">
    <Skills />
    </Route>
    <Route path="/research">
    <Research />
    </Route>
    <Route path="/publication">
    <Publication />
    </Route>
    <Route path="/honors">
    <Honors />
    </Route>
    <Route path="/contact">
    <Contact />
    </Route>
    </div>
    </Router>
    );
  }

  export default App;
