import React, { Component } from 'react';
import {
    Link
} from "react-router-dom";
import { Fade } from "react-awesome-reveal";

class Card extends Component {

render() {
            return (
                    <div id={this.props.item} className="card">
                      <img src={this.props.img}></img>
                      <a className="card_a" href={this.props.tolink}><h5 className="card_title">{this.props.title}</h5></a>
                      <h5 className="card_subtitle">{this.props.subtitle}</h5>
                      <div className="card_desc">{this.props.description}
                        <div style={{"margin-top":"1vw"}}>
                        {this.props.tolink &&
                          <a className="papera" href={this.props.tolink} style={{"margin-right":"1vw"}}>[Link]</a>
                        }
                        {this.props.topaper &&
                          <a className="papera" href={this.props.topaper} style={{"margin-right":"1vw"}}>[Paper]</a>
                        }
                        {this.props.todataset &&
                          <a className="papera" href={this.props.todataset} style={{"margin-right":"1vw"}}>[Dataset]</a>
                        }
                        </div>
                      </div>
                    </div>
                )
        }
    }

export default Card
